<template>
  <section>
    <el-row>
      <el-col :span="8">
        <el-input v-model="search.searchText" suffix-icon="el-icon-search"
        @keyup.enter.native="getData()" clearable
        @blur="getDataBySearch()"
         placeholder="输入您想查询的药品名称/药品代码/输入码"></el-input>
      </el-col>
      <el-col :span="15" align="right">
        <el-button @click="handelAddItem"
         size="small" type="primary" icon="el-icon-plus">新 增</el-button>
      </el-col>
      <el-col :span="24" style="margin-top: 16px">
        <el-table :data="tableData"
        @cell-dblclick="handelView"
         style="width: 100%;">
         <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="主治:">
                  <span>{{ props.row.drugattend }}</span>
                </el-form-item>
                <el-form-item label="用法用量:">
                  <span>{{ props.row.usage }}</span>
                </el-form-item>
                <el-form-item label="药品规格:">
                  <span>{{ props.row.packageunits }}</span>
                </el-form-item>
                <el-form-item label="来源:">
                  <span>{{ props.row.drugsource }}</span>
                </el-form-item>
                <el-form-item label="注意事项:">
                  <span>{{ props.row.matterattend }}</span>
                </el-form-item>
                <el-form-item label="药品售价:">
                  <span>{{ props.row.lprice }}</span>
                </el-form-item>
                <el-form-item label="药量上限:">
                  <span>{{ props.row.upperlimit }}</span>
                </el-form-item>
                <el-form-item label="药量下限:">
                  <span>{{ props.row.lowerlimit }}</span>
                </el-form-item>
                <el-form-item label="HIS对应编码:">
                  <span>{{ props.row.hiscorrecode }}</span>
                </el-form-item>
                <el-form-item label="单位名称:">
                  <span>{{ props.row.unitname }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
            prop="drugcode"
            label="药品编码"
            width="100">
          </el-table-column>
          <el-table-column
            prop="drugname"
            label="药品名称"
            width="180">
          </el-table-column>
          <el-table-column prop="othername" show-overflow-tooltip label="药品别名">
          </el-table-column>
          <el-table-column
            prop="drugfunction"
            label="药品功能">
          </el-table-column>
          <el-table-column
            prop="inputcode"
            label="输入码">
          </el-table-column>
          <el-table-column
            label="操作" header-align="center" width="120">
            <template slot-scope="scope">
              <el-button-group>
                <span class="icon-button-img" @click="handelDelete(scope.row)" ><img src="../../../assets/imgs/delete.png"></span>
                <span class="icon-button-img" @click="handelUpdata(scope.row)"><img src="../../../assets/imgs/edit.png"></span>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="24" class="toolbar">
        <el-pagination background layout="total, prev, pager, next, jumper" @current-change="handelPageChange" :current-page="search.pageNumber" :page-size="search.pageSize" :total="total" style="float:right;">
        </el-pagination>
      </el-col>
    </el-row>
    <el-dialog :title="'中药管理 ' + title" center
    width="80%"
    @close="dialogClose" fullscreen
    :visible.sync="dialog_item">
    <el-form :model="formData" label-width="100px"
        :rules="rules" ref="ruleForm">
      <el-row>
        <el-col :span="8">
          <el-form-item label="药品编码">
            <el-input v-model="formData.drugcode" disabled></el-input>
          </el-form-item>
            <!-- <el-input v-model="formData.drugmodelname" readonly></el-input> -->
            <el-form-item label="药品类别" prop="drugmodelname">
            <el-select v-model="formData.drugmodelcode"
            style="width: 100%"
            @change="handelSelectChangeType"
            placeholder="请选择">
              <el-option
                v-for="item in drugmodel"
                :key="item.drugmodelcode"
                :label="item.drugmodelname"
                :value="item.drugmodelcode">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="单位" prop="units">
            <el-select v-model="formData.units"
            style="width: 100%"
            @change="handelSelectChangeUnitname"
            placeholder="请选择">
              <el-option
                v-for="item in unitAllList"
                :key="item.unitcode"
                :label="item.unitname"
                :value="item.unitcode">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="药量上限">
            <el-input-number style="width:100%" v-model="formData.upperlimit" controls-position="right" :precision="2"
             :min="0" :max="100"></el-input-number>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="药品名称" prop="drugname">
            <el-input v-model="formData.drugname" @blur="getPinCode"></el-input>
          </el-form-item>
          <el-form-item label="药品别名">
            <el-input v-model="formData.othername"></el-input>
          </el-form-item>
          <el-form-item label="药品售价">
            <el-input v-model="formData.lprice"></el-input>
          </el-form-item>
          <el-form-item label="药量下限">
            <el-input-number style="width:100%;" v-model="formData.lowerlimit" controls-position="right" :precision="2"
             :min="0" :max="100"></el-input-number>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="输入码" prop="inputcode">
            <el-input v-model="formData.inputcode"></el-input>
          </el-form-item>
          <el-form-item label="HIS对应编码">
            <el-input v-model="formData.hiscorrecode"></el-input>
          </el-form-item>
          <el-form-item label="停用">
            <el-checkbox v-model="formData.stopflag"></el-checkbox>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="来源">
            <el-input v-model="formData.drugsource"></el-input>
          </el-form-item>
        </el-col>
        </el-row>
          <el-row class="select-list-box">
            <el-col :span="14" class="tab-btn">
              <span data-index='1' class="tab-btn-active" @click="handelTypeChange">炮制</span>
              <span data-index='2' @click="handelTypeChange">性味</span>
              <span data-index='3' @click="handelTypeChange">归经</span>
               <span data-index='4' @click="handelTypeChange">引经</span>
            </el-col>
            <el-col :span="24" class="select-caoyao" v-if="caoyaoYaoXing.length > 0" >
              <div class="caoyao-item">
                <el-checkbox-group @change="handelYaoXingChange" v-model="checkList" class="caoyao-yaoxing-list">
                  <el-checkbox
                  :label="getItemLabel(item)"
                  v-for="item in caoyaoYaoXing"
                  :key="item.meridianCode"></el-checkbox>
                </el-checkbox-group>
              </div>
            </el-col>
          </el-row>
        <el-row style="margin-top: 18px;">
          <el-col :span="12">
            <el-form-item label="功能">
              <el-input type="textarea" v-model="formData.drugfunction"></el-input>
            </el-form-item>
            <el-form-item label="用法用量">
              <el-input type="textarea" v-model="formData.usage"></el-input>
            </el-form-item>
            <el-form-item label="注意事项">
              <el-input type="textarea" v-model="formData.matterattend"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
              <el-form-item label="主治">
              <el-input type="textarea" v-model="formData.drugattend"></el-input>
            </el-form-item>
            <el-form-item label="特殊用法">
              <el-input type="textarea" v-model="formData.specialusage"></el-input>
            </el-form-item>
            <el-form-item label="贮藏">
              <el-input type="textarea" v-model="formData.drugstorage"></el-input>
            </el-form-item>
          </el-col>
        <el-col :span="24">
          <el-form-item label="备注">
            <el-input type="textarea" v-model="formData.remark"></el-input>
          </el-form-item>
        </el-col>
        <el-col class="toolbar" align='center'>
          <el-button size="small" @click="dialog_item=false">取消</el-button>
          <el-button type="primary" size="small" v-if="buttonShow" @click="handelSubmit()">确定</el-button>
        </el-col>
      </el-row>
      </el-form>
    </el-dialog>
  </section>
</template>
<script>
import {
  deleteWesternDistionary, // 删除中中药
  updateWesternDistionary, // 修改药品信息
  getDrugDictDistionaryTable, // 中药列表
  insertWesternDistionary, // 添加药物
  getTasteOrMeridian, // 性味
  getProcess, // 炮制
} from '../../../api'
import {convertPinyin} from '../../../util/getPinYin'
import {mapState} from 'vuex'
export default {
  computed:{
    ...mapState('database',{
      unitAllList: state => state.unitAllList,
      drugWayList: state => state.drugWayList,
      dosageList: state => state.dosageList,
    }),
  },
  data(){
    return{
      tableData: [{date:22,name:'abc'}], //表格数据
      dialog_item: false, // 弹窗显示
      formData: {
        upperlimit: '0.00',
        lowerlimit: '0.00'
      },
      opType: 'add', // 操作标记
      total: 0, // 分页 总条数
      title: '',
      buttonShow: false, // 确定是否显示
      caoyaoYaoXing: [], // 性味 归经 引经
      checkFlage: '1', // 标签标记
      checkList: [], // 当前选择数组
      caoyaoPaozhi:[], // 炮制
      drugmeridianList: [], //归经
      drugyinmeridianList: [], // 引
      drugtaste: [], // 性味
      tempSaveData: '', // 临时待保存数据
      xingweiSavedData:{ list: [], isSaved: false }, // 性味列表 缓存请求
      paozhiSavedData:{ list: [], isSaved: false }, // 炮制列表 缓存请求
      yinjingSavedData:{ list: [], isSaved: false }, // 引经列表 缓存请求
      search:{
        searchText: '',
        pageNumber: 0,
        pageSize: 10,
      },
      drugmodel: [ // 药品类别
        {drugmodelcode: 2, drugmodelname: '中药'},
        {drugmodelcode: 3, drugmodelname: '中成药'},
        {drugmodelcode: 4, drugmodelname: '卫药'},
      ],
      rules:{
        drugname: [{ required: true, message: '请输入药品名称', trigger: 'blur' }],
        inputcode: [{ required: true, message: '请输入输入码', trigger: 'blur' }],
        drugcode: [{ required: true, message: '请输入药品编码', trigger: 'blur' }],
        drugmodelname: [{ required: true, message: '请选择药品类别', trigger: 'blur' }],
        // units: [{ required: true, message: '请输入输入码', trigger: 'blur' }],
      }
    }
  },
  methods:{
    handelSubmit(){ // 确定操作
      if(this.opType == 'add'){
        this.funCheckFormData()
      }else{
        this.handelUpdataXiYao(this.formData)
      }
    },
    funCheckFormData(){ // 检查数据
      this.$refs.ruleForm.validate((valid) => {
        if(valid){
          this.handelInsertYC(this.formData)
        }
      })
    },
    getItemLabel(item){ // 返回列表项标签
      let str = ''
      if(item.tasteCode){
        str = item.tasteCode + ":" + item.tasteName
      }
      if(item.meridianCode){
        str = item.meridianCode + ":" + item.meridianName
      }
      if(item.processcode){
        str = item.processcode + ":" + item.processname
      }
      return str
    },
    handelTypeChange(e){ // 草药选择页 标签切换
      document.querySelector(".tab-btn .tab-btn-active").className = ''
      e.target.className = "tab-btn-active"
      this.checkFlage = e.target.dataset.index
      this.caoyaoYaoXing = []
      this.checkList = []
      this.handleGetYaoXing()
      this.funGetCheckListData()
    },
    handleGetYaoXing(){ // 药性相关
      console.log(this.checkList)
    },
    handelYaoXingChange(){ // 列表选择
      console.log(this.checkList)
      if(this.checkFlage == '1'){
        this.caoyaoPaozhi = [...this.checkList]
      }
      if(this.checkFlage == '2'){
        this.drugtaste = [...this.checkList]
      }
      if(this.checkFlage == '3'){
        this.drugmeridianList = [...this.checkList]
      }
      if(this.checkFlage == '4'){
        this.drugyinmeridianList = [...this.checkList]
      }
    },
    funGetCheckListData(){ // 获取多选列表数据
      if(this.checkFlage == '2'){
        this.checkList = [...this.drugtaste]
        if(this.xingweiSavedData.isSaved){
          this.caoyaoYaoXing = this.xingweiSavedData.list
          return
        }
        getTasteOrMeridian({flag: 0}).then(res=>{
          if(res.data.code == '0'){
            this.xingweiSavedData.list = res.data.data
            this.xingweiSavedData.isSaved = true
            this.caoyaoYaoXing = res.data.data
          }else{
            this.caoyaoYaoXing = []
          }
        })
        return
      }
      if(this.checkFlage == '3'){
        this.checkList = [...this.drugmeridianList]
        if(this.yinjingSavedData.isSaved){
          this.caoyaoYaoXing = this.yinjingSavedData.list
          return
        }
        getTasteOrMeridian({flag: 1}).then(res=>{
          if(res.data.code == '0'){
            this.yinjingSavedData.list = res.data.data
            this.yinjingSavedData.isSaved = true
            this.caoyaoYaoXing = res.data.data
          }else{
            this.caoyaoYaoXing = []
          }
        })
        return
      }
      if(this.checkFlage == '4'){
        this.checkList = [...this.drugyinmeridianList]
        if(this.yinjingSavedData.isSaved){
          this.caoyaoYaoXing = this.yinjingSavedData.list
          return
        }
        getTasteOrMeridian({flag: 1}).then(res=>{
          if(res.data.code == '0'){
            this.yinjingSavedData.list = res.data.data
            this.yinjingSavedData.isSaved = true
            this.caoyaoYaoXing = res.data.data
          }else{
            this.caoyaoYaoXing = []
          }
        })
        return
      }
      if(this.checkFlage == '1'){ // drugprocess
        this.checkList = [...this.caoyaoPaozhi]
        if(this.paozhiSavedData.isSaved){
          this.caoyaoYaoXing = this.paozhiSavedData.list
          return
        }
        getProcess().then(res=>{
          if(res.data.code == '0'){
            this.paozhiSavedData.list = res.data.data
            this.paozhiSavedData.isSaved = true
            this.caoyaoYaoXing = res.data.data
          }else{
            this.caoyaoYaoXing = []
          }
        })
        return
      }
    },
    getPinCode(){ // 获取拼音码
     if(this.formData.drugname)
     this.formData.inputcode = convertPinyin(this.formData.drugname)
    },
    handelSelectChangeDosagename(value){ // 选择切换
      let arr = this.dosageList.forEach(element => {
        if(element.dosagecode == value){
          this.formData.dosagename = element.dosagename
        }
      });
    },
    handelSelectChangeDrugwayname(value){
      let arr = this.drugWayList.forEach(element => {
        if(element.drugwaycode == value){
          this.formData.drugwayname = element.drugwayname
        }
      });
    },
    handelSelectChangeUnitname(value){
      let arr = this.unitAllList.forEach(element => {
        if(element.unitcode == value){
          this.formData.unitname = element.unitname
        }
      });
    },
    handelSelectChangeSinglemeasureunitname(value){
      let arr = this.unitAllList.forEach(element => {
        if(element.unitcode == value){
          this.formData.singlemeasureunitname = element.unitname
        }
      });
    },
    handelSelectChangePrescriptionunitname(value){
      let arr = this.unitAllList.forEach(element => {
        if(element.unitcode == value){
          this.formData.prescriptionunitname = element.unitname
        }
      });
    },
    handelAddItem(){ // 添加
      this.title = '新增'
      this.opType = 'add'
      this.formData = { // 初始默认值 unitcode 1097 unitname 粒
        inputcode: '',
        drugmodelcode: 2,
        drugmodelname: '中药',
        units: 1096,
        unitname: 'g',
        stopflag: false,
        drugflag: 0, // 必填 表示中药
       // drugcode: '',
        drugname: '',
        othername: '',
        upperlimit: 0,
        lowerlimit: 0,
        hiscorrecode: '',
        lprice: '0',
        drugsource: '',
        drugfunction: '',
        usage: '',
        matterattend: '',
        drugattend: '',
        specialusage: '',
        drugstorage: '',
        remark: '',
      }
      this.dialog_item = true
      this.buttonShow = true
      this.resetFields()
    },
    handelDelete(row){ // 列表项删除
      this.$confirm('确定要执行删除操作？','操作提示',{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(()=>{ // 删除确定
        deleteWesternDistionary({drugcode: row.drugcode}).then(res=>{
          if(res.data.code=='0'){
            this.$message.success('删除成功！')
          }
        })
        this.tableData.splice(row,1)
      }).catch(()=>{ // 删除取消

      })
    },
    handelView(row){ // 查看详情
      this.title = '查看'
      this.opType = 'view'
      this.formData = {...row}
      this.funGetCheckListData()
      this.dialog_item = true
    },
    handelUpdata(row){
      this.title = '修改'
      this.opType = 'edit'
      this.buttonShow = true
      this.formData = row
      this.tempSaveData = row.drugname
      this.funSetDataToSave()
      this.funGetCheckListData()
      this.dialog_item = true
    },
    dialogClose(){ // 对话框关闭回调
      this.buttonShow = false
      this.formData = {} //  移除表单数据
       this.caoyaoPaozhi = []
       this.drugtaste = []
       this.drugmeridianList = []
       this.drugyinmeridianList = []
    },
    getData(){ // 获取列表数据
      getDrugDictDistionaryTable(this.search).then(res=>{
        if(res.data.code=="0"){
          this.tableData = res.data.data.rows
          this.total = res.data.data.total
        }else{
          this.tableData = []
          this.total = 0
        }
      })
    },
    handelSelectChangeType(value){ // 选择药品类别
      let arr = this.unitAllList.forEach(element => {
        if(element.unitcode == value){
          this.formData.drugmodelname = element.drugmodelname
        }
      });
    },
    getDataBySearch(){ // 搜索框查询
      if(this.search.searchText){
        this.search.pageNumber = 1
        this.getData()
      }
    },
    handelPageChange(page){ // 翻页
      this.search.pageNumber = page
      this.getData()
    },
    resetFields(){ // 重置el字段检查
      this.$nextTick(()=>{
      //  this.$refs.ruleForm.resetFields()
      })
    },
    handelInsertYC(data){ // 添加数据
    this.funSetListDataToForm()
      insertWesternDistionary(data).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('添加成功!')
          this.getData()
          this.dialog_item = false
        }else{
          this.$message.error('添加失败! ' + res.data.msg)
        }
      })
    },
    funSetListDataToForm(){ // 选项列数据存储到表单 提交前调用
      this.formData.drugprocess = this.caoyaoPaozhi.join(';')
      this.formData.drugtaste = this.drugtaste.join(';')
      this.formData.drugmeridian = this.drugmeridianList.join(';')
      this.formData.drugyinmeridian = this.drugyinmeridianList.join(';')
    },
    funSetDataToSave(){ // 编辑先存储各选项数据
      this.caoyaoPaozhi = this.formData.drugprocess?this.formData.drugprocess.split(';'):[]
      this.drugtaste = this.formData.drugtaste?this.formData.drugtaste.split(';'):[]
      this.drugmeridianList = this.formData.drugmeridian?this.formData.drugmeridian.split(';'):[]
      this.drugyinmeridianList = this.formData.drugyinmeridian?this.formData.drugyinmeridian.split(';'):[]
    },
    handelUpdataXiYao(data){ // 更新数据
      this.formData.drugname2 = this.formData.drugname
      this.formData.drugname = this.tempSaveData
      this.funSetListDataToForm()
      updateWesternDistionary(data).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('修改成功!')
          this.getData()
          this.dialog_item = false
        }else{
          this.$message.error('修改失败')
        }
      })
    }
  },
  mounted(){
    this.getData() // 列表初始数据
    this.funGetCheckListData()
  }
}
</script>
<style scoped>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand span{
    display: inline-block;
    max-width: 426px;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
  }
  .demo-table-expand label {
    width: 90px;
    color: #aaa;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .select-list-box{
    margin-left: 42px;
    height: 140px;
    border: 1px solid #ccc;
  }
  .tab-btn span{
  display: inline-block;
  height: 40px;
  width: 50px;
  margin-right: 20px;
  font-size: 16px;
  color: #333;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
}
.el-checkbox__label{
  width: 60px !important;
}
.caoyao-yaoxing-list label:first-child {
    margin-left: 31px;
}
.tab-btn .tab-btn-active{
  color: #BF7045;
  border-bottom: 2px solid #BF7045;
}
.select-caoyao{
  margin: 8px 0;
  background-color: #FAF7F6;
  height: 90px;
  overflow-y: scroll;
}
